.card {
  box-shadow: 0px 17px 15px rgba(17, 17, 17, 0.77);
  width: 420px;
  height: 340px;
  margin: 3em;
  -webkit-perspective: 1500px;
          perspective: 1500px;
  margin-bottom: 150px; }
  .card .content {
    position: relative;
    width: 100%;
    height: 100%;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transition: -webkit-transform 0.8s cubic-bezier(0.75, 0, 0.85, 1);
    transition: -webkit-transform 0.8s cubic-bezier(0.75, 0, 0.85, 1);
    transition: transform 0.8s cubic-bezier(0.75, 0, 0.85, 1);
    transition: transform 0.8s cubic-bezier(0.75, 0, 0.85, 1), -webkit-transform 0.8s cubic-bezier(0.75, 0, 0.85, 1); }

.more {
  display: none; }
  .more:checked ~ .content {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg); }

.front,
.back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  border-radius: 6px; }
  .front .inner,
  .back .inner {
    height: 100%;
    display: grid;
    padding: 1.5em;
    -webkit-transform: translateZ(80px) scale(0.94);
            transform: translateZ(80px) scale(0.94); }

.front {
  background-color: #fff;
  background-size: cover;
  background-position: center center; }
  .front:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    border-radius: 6px;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    background: -webkit-linear-gradient(50deg, #921919, rgba(255, 242, 166, 0.514));
    background: linear-gradient(40deg, #921919, rgba(255, 242, 166, 0.514)); }
  .front .inner {
    grid-template-rows: 5fr 1fr 1fr 2fr 1fr;
    justify-items: center;
    text-align: center; }
  .front h2 {
    grid-row: 2;
    margin-bottom: 0.3em;
    text-transform: uppercase;
    letter-spacing: 3px;
    color: #fff;
    font-weight: 500;
    text-shadow: 0 0 6px rgba(0, 0, 0, 0.1); }
  .front h5 {
    color: #fff; }
  .front h6 {
    color: #fff; }
  .front .rating {
    grid-row: 3;
    color: rgba(255, 255, 255, 0.8);
    font-size: 14px;
    display: flex;
    flex-flow: row nowrap; }
    .front .rating i {
      margin: 0 1px; }

.back {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  background: -webkit-linear-gradient(50deg, #921919, rgba(255, 242, 166, 0.514));
  background: linear-gradient(40deg, #921919, rgba(255, 242, 166, 0.514));
  border: 2px solid #f0f0f0; }
  .back .inner {
    grid-template-rows: 1fr 2fr 1fr 2fr 14fr 1fr 1fr;
    grid-template-columns: repeat(4, auto);
    grid-column-gap: 0.8em;
    justify-items: center; }
  .back .description {
    grid-row: 5;
    grid-column: 1/-1;
    font-size: 0.86em;
    border-radius: 5px;
    font-weight: 600;
    line-height: 1.4em;
    overflow: auto;
    color: white;
    padding-right: 10px; }
  .back .price {
    grid-column: 3/-1;
    justify-self: right; }
  .back .button {
    grid-column: 1/-1;
    justify-self: center; }

.button {
  grid-row: -1;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 600;
  cursor: pointer;
  display: block;
  padding: 0 1.5em;
  height: 3em;
  line-height: 2.9em;
  min-width: 3em;
  background-color: transparent;
  border: solid 2px #fff;
  color: #fff;
  border-radius: 4px;
  text-align: center;
  left: 50%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
  text-shadow: 0 0 6px rgba(0, 0, 0, 0.3); }
  .button:hover {
    background-color: #fff;
    box-shadow: 0 0 50px rgba(0, 0, 0, 0.5);
    text-shadow: none;
    color: black; }
  .button.return {
    line-height: 3em;
    color: white;
    border-color: white;
    text-shadow: none; }
    .button.return:hover {
      background-color: black;
      color: #fff;
      box-shadow: none; }

